import React, { useState } from 'react';
import { DropDown } from './DropDown';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal, uiOpenModal } from '../../actions/ui';

export const DatosUsuario = () => {

    // Redux
    // Para hacer dispatch de acciones
    const dispatch = useDispatch();
    // Para obtener datos del store
    const { modalOpen } = useSelector(state => state.uiReducer);

    // Modal handlers
    const handleModal = () => {
        if (modalOpen) {
            dispatch(uiCloseModal());
        } else {
            dispatch(uiOpenModal());
        }
    }

    return (
        <div className="dropright">
            <div className="menu__contendedor-datos-usuario" onClick={handleModal}>
            <hr className="divider-sidebar"/>
            <div className="menu__tarjeta_usuario">
                <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e0/Check_green_icon.svg/1200px-Check_green_icon.svg.png" alt="img" className="menu__img-usuario"></img>
                <div className="menu__nombre-posicion">
                    <p className="menu__nombre-usuario">Víctor Hugo Vázquez Gómez</p>
                    <p className="menu__posicion-usuario">Programador</p>
                </div>
            </div>
            <hr className="divider-sidebar"/>
            </div>
            
            {
                modalOpen && <DropDown/>
            }

        </div>
    )
}
