import React from 'react';
import { useDropzone } from 'react-dropzone';

export const FormFileUpload = ({onDrop, accept}) => {

    // Inicializar el hook useDropzone con las opciones
    /**
     * El hook useDropzone expone dos funciones llamada getRootProps y 
     * getInputProps y también el booleano isDragActive
     * 
     * - getRootProps: Esta función está establecida en base al elemento padre.
     *               Este propiedad determina el ancho y alto de el área de "drop n' drag"
     * - getInputProps: Son los props pasados al elemento input. Nos permite realizar 
     *                  eventos de clic o de arrastrar para obtener archivos.
     * - Todas las opciones relacionadas a los archivos que le pasamos a useDropzone
     *   son establecidas al elemento input. Por ejemplo, si quieres soportar que solo 
     *   se suba un archivo, puedes pasar `multiple: false`. Automáticamente dropzone 
     *   requerirá de un solo archivo para aceptarlo.
     * - isDragActive: Es establecido a verdadero si los archivos se arrastran por encima
     *                 del área de la zona del componente. Esto es útil para hacer estilos
     *                 basados en esta variable.
     * 
     * Para más información, visita los siguientes links: 
     * 
     *  - https://github.com/react-dropzone/react-dropzone
     *  - https://blog.logrocket.com/react-drag-and-drop/ (De este tutorial me basé)
     * 
     */
    const { getRootProps, getInputProps, isDragActive } = useDropzone({onDrop, accept});

    return (
        <div {...getRootProps()} className="dropzone-wrapper">
            <input className="dropzone-input" {...getInputProps()} ></input>
            <div className="text-center">
                {
                    isDragActive ? (
                        <p className="dropzone-content">
                            Presiona para subir un archivo
                        </p>
                    ) : (
                        <div className="dropzone-content-wrapper">
                            <p className="dropzone-content">
                                Arrastra y suelta el archivo aquí
                            </p>
                            <button className="dropzone-button">
                                O da clic aquí
                            </button>
                        </div>
                    )
                }
            </div>
        </div>
    )
}
