import React, { useCallback } from 'react';
import { FormFileUpload } from './FormFileUpload';

export const FormDetails = () => {

    // Función onDrop para el componente de subida de archivos
    // La razón por la que está en un useCallback es porque 
    // en el futuro tendremos que hacer varios procesos que 
    // pueden ser pesados, y es mejor hacer que se ejecute solo
    // cuando lo invocamos y no cuando el componente se renderice.
    const onDrop = useCallback( acceptedFiles => {
        // Este callback será llamado después de que los archivos hayan
        // sido soltados, obtendremos acceptedFiles. Si quieres,
        // también puedes acceder a los archivos rechazados también.
        console.log(acceptedFiles);
    }, []);

    return (
        <div className="form__form-details">
            <div className="form_form-details-wrapper">
                <p className="form_form-details-title">Resumen General</p>
                <p className="form__form-details-description">
                    Se solicita requisitar el archivo Excel 
                    adjunto con los resultados obtenidos de 
                    los programas sustantivos relacionados 
                    con el programa de postgrado durante el 
                    período a evaluar: Investigación, 
                    Desarrollo Tecnológico e Innovación; 
                    Formación de Recursos Humanos; 
                    Transferencia de Tecnología, Vinculación, 
                    Servicios y Difusión y Divulgación.</p>
            </div>
            <hr className="divider-sidebar-form"/>
            <form className="form__form-wrapper" id="form1">
                    <p className="form__input-wrapper">
                        <label className="form__label">Nombre del instituto o centro</label>
                        <input type="text" className="form__input"></input>
                    </p>
                    <p className="form__input-wrapper">
                        <label className="form__label">Nombre del posgrado</label>
                        <input type="text" className="form__input"></input>
                    </p>
                    <p className="form__input-wrapper">
                        <label className="form__label">Entidad Federativa</label>
                        <input type="text" className="form__input"></input>
                    </p>
                    <p className="form__input-wrapper">
                        <label className="form__label">Municipio</label>
                        <input type="text" className="form__input"></input>
                    </p>
                    <p className="form__input-wrapper">
                        <label className="form__label">Lineas de Investigación</label>
                        <input type="text" className="form__input"></input>
                    </p>
            </form>
            <hr className="divider-sidebar-form"/>
            {/* Componente para la subida de archivos */}
            {/* En la propiedad accept, si queremos PDFs, usamos 'application/pdf' */}
            <FormFileUpload onDrop={onDrop} accept={"image/*"}/>
            <hr className="divider-sidebar-form"/>
            <button type="submit" className="form__button" form="form1" value="Submit">
                Guardar Cambios
            </button>
        </div>
    )
}
