import React from 'react';
// import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';

/**
 * Componente para el dropdown cuando se da clic en los datos del usuario
 */
export const DropDown = () => {

    // Redux
    // Para hacer dispatch de acciones
    const dispatch = useDispatch();
    // Para obtener datos del store
    const { modalOpen } = useSelector(state => state.uiReducer);

    // Modal handlers
    const closeModal = () => {
        dispatch(uiCloseModal());
    }

    return (
        <div className="dropdown">
            <div class="dropdown-triangle"></div>
            <div className="menu__dropdown-menu">
                <hr className="menu__dropdown-hr"></hr>
                <p className="menu__dropdown-greet">
                    ¡Hola, Víctor Hugo Vázquez Gómez!
                </p>
                <p className="menu__dropdown-position">
                    Jefe de posgrado
                </p>
                <hr className="menu__dropdown-hr"></hr>
                <p className="menu__dropdown-edit">
                    Editar perfil
                </p>
                <p className="menu__dropdown-logout">
                    Cerrar sesión
                </p>
            </div>
        </div>
    )
}
